import React from 'react';

const Item = ({ partText, id, price, onRemove }) => {
    return (
        <li className='list-group-item border-none bg-dark mt-2 mb-2' style={{ listStyle: 'none' }}>
            <div className='input-group'>
                <span className='input-group-text'>P{price}</span>
                <input type='text' className='form-control' defaultValue={partText} readOnly />
                <input onClick={() => onRemove(id)} type='button' value='X' className='input-group-text btn btn-danger' />
            </div>
        </li>
    );
};

export default Item;
