import React, { useContext, useEffect, useRef, useState } from 'react';
import Calculation from '../app data/Calculation';
import ChangeBtn from './ChangeBtn';
import { useNavigate } from 'react-router-dom';

const Deadline = () => {
    const { projectPrice, setDeadline, projectName, deadline } = useContext(Calculation);
    const [isValid, setIsValid] = useState(false);
    const navigator = useNavigate();
    const dateInput = useRef(null);

    // Checking
    useEffect(() => {
        dateInput.current.defaultValue = deadline;
        setIsValid(deadline != '');
        if (projectName == '') {
            navigator('/');
        }
    }, [projectName]);

    const pickDate = e => {
        const datePicked = e.target.value;

        setDeadline(datePicked);
        if (datePicked != '') {
            if (Date.parse(datePicked) > Date.now()) {
                setIsValid(true);
            } else {
                setDeadline('');
                setIsValid(false);
            }
        } else {
            setIsValid(false);
        }
    };

    return (
        <>
            <header className=''>
                <div className='d-flex gap-3 justify-content-between align-items-center'>
                    <h3 className='h4'>Project deadline </h3>
                    <p>P{projectPrice}</p>
                </div>
            </header>
            <input ref={dateInput} type='date' onChange={pickDate} className='form-control' name='' id='' />
            <div
                className='d-flex mt-3  justify-content-end gap-3 pt-3'
                style={{
                    borderTop: '1px solid #ccc',
                }}>
                <ChangeBtn state={true} type={'secondary'} text={'Back'} link={'/features'} />
                <ChangeBtn state={isValid} type={'warning'} text={'Next'} link={'/price'} />
            </div>
        </>
    );
};

export default Deadline;
