import React, { useContext } from 'react';
import { Outlet } from 'react-router-dom';
import Calculation from '../app data/Calculation';

const Main = () => {
    return (
        <main
            className='d-flex mt-2'
            style={{
                minHeight: 'max(600px, 80vh)',
            }}>
            <section
                className='m-auto'
                style={{
                    width: 'min(400px, 95vw)',
                }}>
                <Outlet />
            </section>
        </main>
    );
};

export default Main;
