import React, { useContext, useEffect, useRef } from 'react';
import ChangeBtn from './ChangeBtn';
import Calculation from '../app data/Calculation';
import PriceSection from './PriceSection';
import html2canvas from 'html2canvas';
import { isLabelWithInternallyDisabledControl } from '@testing-library/user-event/dist/utils';
import { useNavigate } from 'react-router-dom';

const ProjectPrice = () => {
    const { projectName, parts, pricePerPart, features, pricePerFeature, projectPrice, deadline, deadlinePrice } = useContext(Calculation);
    const priceContainer = useRef(null);
    const navigator = useNavigate();

    // Checking
    useEffect(() => {
        if (projectName == '') {
            navigator('/');
        }
    }, [projectName]);

    const savePrice = () => {
        html2canvas(priceContainer.current).then(canvas => {
            // Convert the canvas to an image data URL
            const dataUrl = canvas.toDataURL();

            // Create a link element to download the image
            const link = document.createElement('a');
            link.href = dataUrl;
            link.download = 'project-price.png';
            link.click();
        });
    };

    return (
        <>
            <section
                ref={priceContainer}
                className='bg-light text-dark rounded'
                style={{
                    minHeight: '500px',
                    position: 'relative',
                    padding: '20px',
                    paddingBottom: '70px',
                }}>
                <header
                    className='pt-1 mb-3 '
                    style={{
                        borderBottom: '3px dashed #222',
                    }}>
                    <h3 className='h4'>Project Price</h3>
                </header>
                <div>
                    <p className='h6'>
                        Project name: <span className='fw-bold h5'>{projectName}</span>
                    </p>
                    <PriceSection header={'Parts total'} totalPrice={parts.length * pricePerPart} items={parts} pricePerItem={pricePerPart} />
                    <PriceSection header={'Features total'} totalPrice={features.length * pricePerFeature} items={features} pricePerItem={pricePerFeature} />
                    <PriceSection header={'Deadline'} totalPrice={deadlinePrice} items={[{ name: deadline, id: 8 }]} pricePerItem={deadlinePrice} />
                </div>
                <footer
                    className='pt-3'
                    style={{
                        borderTop: '3px dashed #222',
                        position: 'absolute',
                        bottom: '0',
                        width: '90%',
                    }}>
                    <p className='h4 d-flex justify-content-between'>
                        Total Price:{' '}
                        <span
                            className='fw-bold '
                            style={{
                                textDecoration: 'underlined !important',
                            }}>
                            {projectPrice}
                        </span>
                    </p>
                </footer>
            </section>
            <div
                className='d-flex mt-3  justify-content-end gap-3 pt-3'
                style={{
                    borderTop: '1px solid #ccc',
                }}>
                <ChangeBtn state={true} type={'secondary'} text={'Back'} link={'/deadline'} />
                <button className='btn btn-danger' onClick={() => (window.location.href = '/')}>
                    Reset
                </button>
                <button className='btn btn-warning' onClick={savePrice}>
                    Save
                </button>
            </div>
        </>
    );
};

export default ProjectPrice;
