import React from 'react';
import { CalculationCart } from './app data/Calculation';
import { createBrowserRouter, createRoutesFromElements, Route, RouterProvider } from 'react-router-dom';

// Bootstrap
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap';

// Components
import Header from './components/Header';
import Main from './components/Main';
import FirstStep from './components/FirstStep';
import SecondStep from './components/SecondStep';
import Features from './components/Features';
import Deadline from './components/Deadline';
import ProjectPrice from './components/ProjectPrice';

// Body
document.querySelector('body').classList.add('bg-dark');
document.querySelector('body').classList.add('text-light');

const App = () => {
    const route = createBrowserRouter(
        createRoutesFromElements(
            <Route path='/' element={<Main />}>
                <Route index element={<FirstStep />} />
                <Route path='/second' element={<SecondStep />} />
                <Route path='/features' element={<Features />} />
                <Route path='/deadline' element={<Deadline />} />
                <Route path='/price' element={<ProjectPrice />} />
            </Route>
        )
    );
    return (
        <>
            <CalculationCart>
                <Header />
                <RouterProvider router={route}></RouterProvider>
            </CalculationCart>
        </>
    );
};

export default App;
