import React, { useContext, useEffect, useRef, useState } from 'react';
import Calculation from '../app data/Calculation';
import Item from './Item';
import ChangeBtn from './ChangeBtn';
import { useNavigate } from 'react-router-dom';

const Features = () => {
    const { projectName, projectPrice, features, pricePerFeature, addFeatures, removeFeature } = useContext(Calculation);
    const [toAdd, setToAdd] = useState('');
    const featureInput = useRef(null);
    const navigator = useNavigate();

    // Checking
    useEffect(() => {
        if (projectName == '') {
            navigator('/');
        }
    }, [projectName]);

    const setFeature = () => {
        if (toAdd == '') return;
        addFeatures({ name: toAdd, id: Math.floor(Math.random() * 100000) });
        featureInput.current.value = '';
        setToAdd('');
    };

    return (
        <>
            <header className='d-flex justify-content-between align-items-center'>
                <div className='d-flex gap-3 align-items-center'>
                    <h3 className='h4'>Features P{projectPrice}</h3>
                    <p className='h4'>{features.length}</p>
                </div>
                <button className={`btn btn-secondary btn-sm opacity-${toAdd != '' ? 100 : 50}`} onClick={setFeature}>
                    ADD
                </button>
            </header>
            <ul className='list-group '>
                <ul className='list-group ' style={{ maxHeight: '500px', overflowY: 'auto' }}>
                    {Array.from(features).map(feature => (
                        <Item onRemove={removeFeature} price={pricePerFeature} key={feature.id} id={feature.id} partText={feature.name} />
                    ))}
                </ul>
                <li className='list-group-item border-none bg-dark mt-2 mb-2' style={{ listStyle: 'none' }}>
                    <div className='input-group'>
                        <span className='input-group-text'>P{pricePerFeature}</span>
                        <input type='text' ref={featureInput} onInput={e => setToAdd(e.target.value)} className='form-control' />
                    </div>
                </li>
            </ul>
            <div
                className='d-flex mt-3  justify-content-end gap-3 pt-3'
                style={{
                    borderTop: '1px solid #ccc',
                }}>
                <ChangeBtn state={true} type={'secondary'} text={'Back'} link={'/second'} />
                <ChangeBtn state={features.length > 0 ? true : false} type={'warning'} text={'Next'} link={'/deadline'} />
            </div>
        </>
    );
};

export default Features;
