import React, { useContext, useEffect, useState } from 'react';
import Calculation from '../app data/Calculation';
import ChangeBtn from './ChangeBtn';

const FirstStep = () => {
    const { setProjectName, projectName } = useContext(Calculation);
    const [isValid, setIsValid] = useState(projectName != '' ? true : false);

    return (
        <>
            <input
                onInput={e => {
                    setIsValid(e.target.value != '' ? true : false);
                    setProjectName(v => (v = e.target.value));
                }}
                className='form-control'
                type='text'
                defaultValue={projectName}
                placeholder='Enter project name'
            />
            <div
                className='d-flex mt-3  justify-content-end gap-3 pt-3'
                style={{
                    borderTop: '1px solid #ccc',
                }}>
                <ChangeBtn state={isValid} type={'warning'} text={'Next'} link={'/second'} />
            </div>
        </>
    );
};

export default FirstStep;
