import React from 'react';
import { useNavigate } from 'react-router-dom';

const ChangeBtn = ({ text, link, type, state }) => {
    const navigator = useNavigate();

    return (
        <button onClick={() => (state ? navigator(link) : '')} className={`btn opacity-${state ? 100 : 50} btn-${type}`}>
            {text}
        </button>
    );
};

export default ChangeBtn;
