import React, { useContext, useEffect, useRef, useState } from 'react';
import ChangeBtn from './ChangeBtn';
import Calculation from '../app data/Calculation';
import { useNavigate } from 'react-router-dom';
import Item from './Item';

const SecondStep = () => {
    const { projectName, parts, addPart, pricePerPart, removePart, projectPrice } = useContext(Calculation);
    const [toAdd, setToAdd] = useState('');
    const partInput = useRef(null);
    const navigator = useNavigate();

    // Checking
    useEffect(() => {
        if (projectName == '') {
            navigator('/');
        }
    }, [projectName]);

    const setPart = () => {
        if (toAdd == '') return;
        addPart({ name: toAdd, id: Math.floor(Math.random() * 100000) });
        partInput.current.value = '';
        setToAdd('');
    };

    return (
        <>
            <header className='d-flex justify-content-between align-items-center'>
                <div className='d-flex gap-3 align-items-center'>
                    <h3 className='h4'>Parts P{projectPrice}</h3>
                    <p className='h4'>{parts.length}</p>
                </div>
                <button className={`btn btn-secondary btn-sm opacity-${toAdd != '' ? 100 : 50}`} onClick={setPart}>
                    ADD
                </button>
            </header>
            <ul className='list-group '>
                <ul className='list-group ' style={{ maxHeight: '500px', overflowY: 'auto' }}>
                    {Array.from(parts).map(part => (
                        <Item onRemove={id => removePart(id)} price={pricePerPart} key={part.id} id={part.id} partText={part.name} />
                    ))}
                </ul>
                <li className='list-group-item border-none bg-dark mt-2 mb-2' style={{ listStyle: 'none' }}>
                    <div className='input-group'>
                        <span className='input-group-text'>P{pricePerPart}</span>
                        <input type='text' ref={partInput} onInput={e => setToAdd(e.target.value)} className='form-control' />
                    </div>
                </li>
            </ul>
            <div
                className='d-flex mt-3  justify-content-end gap-3 pt-3'
                style={{
                    borderTop: '1px solid #ccc',
                }}>
                <ChangeBtn state={true} type={'secondary'} text={'Back'} link={'/'} />
                <ChangeBtn state={parts.length > 0 ? true : false} type={'warning'} text={'Next'} link={'/features'} />
            </div>
        </>
    );
};

export default SecondStep;
