import React from 'react';

const PriceSection = ({ header, totalPrice, pricePerItem, items }) => {
    return (
        <>
            <hr />
            <div className='mt-2'>
                <p className='h5'>
                    {header}: P{totalPrice}
                </p>
                <ul
                    className='d-flex'
                    style={{
                        justifyContent: 'start',
                        flexBasis: '65px',
                        flexWrap: 'wrap',
                        columnGap: '25px',
                    }}>
                    {Array.from(items).map(item => (
                        <li key={item.id}>
                            {item.name} P{pricePerItem}
                        </li>
                    ))}
                </ul>
            </div>
        </>
    );
};

export default PriceSection;
